import React from 'react';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';

import LinkedinIcon from '../img/LinkedinIcon.png';
import GithubIcon from '../img/GithubIcon.png';
import CVIcon from '../img/CVIcon.png';


const styles = theme => ({
    Root: {
      backgroundColor: "black",
      color: "white",
      overflowX: "hidden"
    },
    ResponsiveIcons: {
        height: "15vh",
        width: "15vh",
        maxHeight: 100,
        maxWidth: 100
    }
  })

function Footer(props) {
    const { classes } = props;

    return (
        <div className={classes.Root}>
            <Grid container alignContent="space-around" alignItems="center" spacing={0}>
                <Grid item xs={12}>
                    <h3>Contacto</h3>
                </Grid>
                <Grid item xs={4}>
                    <a href="https://www.linkedin.com/in/alejandrofabiancampos/" target="_blank" rel="noopener noreferrer">
                        <img src={LinkedinIcon} alt="Icono de Linkedin" className={classes.ResponsiveIcons}/>
                    </a>
                </Grid>
                <Grid item xs={4}>
                    <a href="https://github.com/AlejandroFabianCampos" target="_blank" rel="noopener noreferrer">
                        <img src={GithubIcon} alt="Icono de Github" className={classes.ResponsiveIcons}/>
                    </a>
                </Grid>
                <Grid item xs={4}>
                    <a href="http://alejandrofabiancampos.tk/" target="_blank" rel="noopener noreferrer">
                        <img src={CVIcon} alt="Icono de CV" className={classes.ResponsiveIcons}/>
                    </a>
                </Grid>
                <Grid item xs={12}>
                    <p>También me podes contactar en <strong>camposalejandrofabian@gmail.com</strong>.</p>
                </Grid>
            </Grid>
            
        </div>
    )
}

export default withStyles(styles)(Footer);
